import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-roadmap',
  templateUrl: './roadmap.component.html',
  styleUrls: ['./roadmap.component.scss']
})
export class RoadmapComponent implements OnInit {
  showQ1 = false;
  showQ2 = false;
  showQ3 = false;
  showQ4 = false;
  showQ5 = false;
  showQ6 = false;
  constructor() { }

  ngOnInit(): void {
  }

  showEvents(qNumber: string) {
    console.log('qNumber', qNumber);
    if (qNumber === 'Q1') {
      this.showQ1 = true;
      this.showQ2 = false;
      this.showQ3 = false;
      this.showQ4 = false;
      this.showQ5 = false;
      this.showQ6 = false;
    } else if (qNumber === 'Q2') {
      this.showQ2 = true;
      this.showQ1 = false;
      this.showQ3 = false;
      this.showQ4 = false;
      this.showQ5 = false;
      this.showQ6 = false;
    } else if (qNumber === 'Q3') {
      this.showQ3 = true;
      this.showQ1 = false;
      this.showQ2 = false;
      this.showQ4 = false;
      this.showQ5 = false;
      this.showQ6 = false;
    } else if (qNumber === 'Q4') {
      this.showQ4 = true;
      this.showQ1 = false;
      this.showQ2 = false;
      this.showQ3 = false;
      this.showQ5 = false;
      this.showQ6 = false;
    } else if (qNumber === 'Q5') {
      this.showQ5 = true;
      this.showQ1 = false;
      this.showQ2 = false;
      this.showQ3 = false;
      this.showQ4 = false;
      this.showQ6 = false;
    } else if (qNumber === 'Q6') {
      this.showQ6 = true;
      this.showQ1 = false;
      this.showQ2 = false;
      this.showQ3 = false;
      this.showQ4 = false;
      this.showQ5 = false;
    }
  }

}
